'use client';

import { useState, useEffect } from 'react';
import { DollarSign, Edit2, Save, X } from 'lucide-react';

type MassPrice = {
  id: number;
  tipo_misa: string;
  nombre: string;
  descripcion: string;
  precio: number;
  is_active: boolean;
  display_order: number;
};

const TIPO_MISA_LABELS: Record<string, string> = {
  simple: 'Misa Simple',
  novenario: 'Novenario (9 días)',
  cabo_ano: 'Cabo de Año',
};

export default function MassPricingPage() {
  const [prices, setPrices] = useState<MassPrice[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [editingId, setEditingId] = useState<number | null>(null);
  const [editForm, setEditForm] = useState({ precio: 0, descripcion: '' });

  useEffect(() => {
    fetchPrices();
  }, []);

  const fetchPrices = async () => {
    try {
      const response = await fetch('/api/mass-types');
      const data = await response.json();
      if (data.success) {
        setPrices(data.data);
      }
    } catch (error) {
      console.error('Error al cargar precios:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const startEdit = (price: MassPrice) => {
    setEditingId(price.id);
    setEditForm({
      precio: price.precio,
      descripcion: price.descripcion,
    });
  };

  const cancelEdit = () => {
    setEditingId(null);
  };

  const saveEdit = async (id: number) => {
    try {
      const response = await fetch(`/api/admin/mass-pricing/${id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(editForm),
      });

      const data = await response.json();

      if (data.success) {
        // Actualizar el estado local
        setPrices(prices.map(p => 
          p.id === id 
            ? { ...p, precio: editForm.precio, descripcion: editForm.descripcion }
            : p
        ));
        setEditingId(null);
        alert('Precio actualizado correctamente');
      } else {
        alert(data.message || 'Error al actualizar el precio');
      }
    } catch (error) {
      console.error('Error:', error);
      alert('Error al actualizar el precio');
    }
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Cargando precios...</p>
        </div>
      </div>
    );
  }

  return (
    <div>
      {/* Header */}
      <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Precios de Misas</h1>
            <p className="text-gray-600 mt-1">Gestiona los precios de los diferentes tipos de misas</p>
          </div>
          <DollarSign className="w-12 h-12 text-green-600" />
        </div>
      </div>

      {/* Price Cards */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {prices.map((price) => (
          <div key={price.id} className="bg-white rounded-lg shadow-sm p-6">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-lg font-semibold text-gray-900">
                {price.nombre}
              </h3>
              {editingId === price.id ? (
                <div className="flex gap-2">
                  <button
                    onClick={() => saveEdit(price.id)}
                    className="p-1 text-green-600 hover:bg-green-50 rounded"
                  >
                    <Save className="w-4 h-4" />
                  </button>
                  <button
                    onClick={cancelEdit}
                    className="p-1 text-red-600 hover:bg-red-50 rounded"
                  >
                    <X className="w-4 h-4" />
                  </button>
                </div>
              ) : (
                <button
                  onClick={() => startEdit(price)}
                  className="p-1 text-blue-600 hover:bg-blue-50 rounded"
                >
                  <Edit2 className="w-4 h-4" />
                </button>
              )}
            </div>

            {editingId === price.id ? (
              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Precio (S/.)
                  </label>
                  <input
                    type="number"
                    value={editForm.precio}
                    onChange={(e) => setEditForm({ ...editForm, precio: parseFloat(e.target.value) })}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Descripción
                  </label>
                  <textarea
                    value={editForm.descripcion}
                    onChange={(e) => setEditForm({ ...editForm, descripcion: e.target.value })}
                    rows={3}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                  />
                </div>
              </div>
            ) : (
              <>
                <div className="text-4xl font-bold text-green-600 mb-4">
                  S/. {price.precio !== undefined ? price.precio.toFixed(2) : '0.00'}
                </div>
                <p className="text-sm text-gray-600">
                  {price.descripcion || 'Sin descripción'}
                </p>
              </>
            )}
          </div>
        ))}
      </div>

      {/* Info */}
      <div className="mt-6 bg-blue-50 border-l-4 border-blue-400 p-4 rounded">
        <div className="flex">
          <div className="ml-3">
            <p className="text-sm text-blue-700">
              <strong>Nota:</strong> Los cambios en los precios se reflejarán inmediatamente en el formulario de reservas.
              Asegúrate de comunicar cualquier cambio de precio con anticipación a los feligreses.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
